<template>
    <p-toolbar class="p-p-1 p-mb-2 p-shadow-1">
        <template #left>
            <p-button
                label="Save"
                class="p-mr-4"
                @click="save"
                v-if="hasUpdatePermission || hasCreatePermission"
            />

            <p-divider layout="vertical" />
            <span>
                FSH: <strong>{{ bloodTests.fsh }}</strong>
            </span>
            <p-divider layout="vertical" />
            <span>
                TSH: <strong>{{ bloodTests.tsh }}</strong>
            </span>
            <p-divider layout="vertical" />
            <span>
                PRL: <strong>{{ bloodTests.prl }}</strong>
            </span>
            <p-divider layout="vertical" />
            <span>
                BhCG: <strong>{{ bloodTests.bhcg }}</strong>
            </span>
            <p-divider layout="vertical" />
            <span>
                TESTO: <strong>{{ bloodTests.testo }}</strong>
            </span>
            <p-divider layout="vertical" />
            <span>
                DHEAS: <strong>{{ bloodTests.dheas }}</strong>
            </span>
            <p-divider layout="vertical" />
            <span>
                AMHP: <strong>{{ bloodTests.amh }}</strong>
            </span>
        </template>

        <template #right>
            <p-button
                label="Cancel Message Lab"
                class="p-mr-2 app-button p-button-sm"
            />
            <p-button
                label="Lab Contacts"
                class="app-button p-button-sm"
                @click="openLabContactsDailog"
            />
        </template>
    </p-toolbar>
</template>

<script>
import { emitter } from "@/event-bus";
import { useStore } from "vuex";
import { computed, inject } from "vue";
import { usePermissionCheck } from "@/composable/users";

export default {
    setup() {
        const store = useStore();

        const bloodTests = computed(
            () => store.state.cycleMonitoring.bloodTests
        );

        const labContactsDailog = inject("labContactsDailog");

        const {
            hasUpdatePermission,
            hasCreatePermission
        } = usePermissionCheck();

        return {
            bloodTests,
            save: () => {
                emitter.emit("cycle-monitoring-toolbar:save");
            },
            openLabContactsDailog: () => {
                labContactsDailog.value = true;
            },
            hasUpdatePermission,
            hasCreatePermission
        };
    }
};
</script>

<style scoped>
strong {
    color: #2196f3;
    font-size: 14px;
}
</style>
